import React, { Component } from 'react';
import { Nav, Navbar, NavbarBrand, NavbarToggler, Collapse, NavItem, Jumbotron  ,Button, Modal, ModalHeader, ModalBody,Form, FormGroup, Input, Label} from 'reactstrap';
import { NavLink } from 'react-router-dom';
class HeaderComponent extends Component {
    constructor(props) {
        super(props);
    
      
        this.state = {
          isNavOpen: false,
          isModalOpen: false,
          isModalOpen2:false
        };
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.toggleModal12 = this.toggleModal12.bind(this);
        this.toggleModal2 = this.toggleModal2.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
     
      }
      toggleModal() {
        this.setState({
          isModalOpen: !this.state.isModalOpen
        });
      }
      toggleModal2() {
        this.setState({
          isModalOpen2: !this.state.isModalOpen2
        });
      }
      toggleModal12() {
        this.setState({
            
          isModalOpen2: !this.state.isModalOpen2,
          isModalOpen: !this.state.isModalOpen
        });
      }
      toggleNav() {
        this.setState({
          isNavOpen: !this.state.isNavOpen
        });
      }
      handleLogin(event) {
        this.toggleModal();
        alert("Username: " + this.username.value + " Password: " + this.password.value
            + " Remember: " + this.remember.checked);
        event.preventDefault();

    }
    handleLogin(event) {
        this.toggleModal2();
        alert("Username: " + this.username.value + " Password: " + this.password.value
            + " Remember: " + this.remember.checked);
        event.preventDefault();

    }

    render() {
        return(
            <div>
                <Navbar dark expand="md" className="na d-block d-sm-none">
              
                       
                        <NavbarBrand className="mr-auto " href="/"><img src='assets/images/logo.png' height="30" width="40" alt='SS Food Factory' /><strong class="pl-2" > SS Food Factory </strong></NavbarBrand>
                        <NavbarToggler onClick={this.toggleNav} className="navicon"/>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar className="d-flex">
                            <NavItem className="ml-5">
                                <NavLink className="nav-link "  to='/home' onClick={this.toggleNav}><span className="fa fa-home fa-lg"></span> <strong> Home</strong></NavLink>
                            </NavItem>
                            <NavItem className="ml-5">
                                <NavLink className="nav-link" to='/aboutus' onClick={this.toggleNav}><span className="fa fa-info fa-lg"></span><strong> About Us</strong></NavLink>
                            </NavItem>
                            <NavItem className="ml-5">
                                <NavLink className="nav-link"  to='/menu' onClick={this.toggleNav}><span className="fa fa-list fa-lg"></span><strong> Menu</strong></NavLink>
                            </NavItem>
                            <NavItem className="ml-5">
                                <NavLink className="nav-link" to='/contactus' onClick={this.toggleNav}><span className="fa fa-address-card fa-lg"></span><strong> Contact Us</strong></NavLink>
                            </NavItem >
                            </Nav>
                            <Nav className="ml-auto" navbar>
                                <NavItem className="ml-5 mr-5">
                                <button type="button" onClick={this.toggleModal} class="btn btn-light"><span className="fa fa-sign-in fa-lg"></span> Login</button>
                                
                                </NavItem>
                            </Nav>
                        </Collapse>
                 
                </Navbar>
                

                <Navbar dark expand="md" className="na2 d-none d-sm-block">
              
                       
                       
                        <NavbarToggler onClick={this.toggleNav} className="ml-5"/>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar className="d-flex">
                            <NavbarBrand className=" m2 " href="/"><img src='assets/images/logo.png' height="30" width="40" alt='SS Food Factory' /><strong class="pl-2" > SS Food Factory </strong></NavbarBrand>
                            <NavItem className="ml-1">
                                <NavLink className="nav-link "  to='/home' ><span className="fa fa-home fa-lg"></span> <strong> Home</strong></NavLink>
                            </NavItem>
                            <NavItem className="ml-1">
                                <NavLink className="nav-link"  to='/menu'><span className="fa fa-list fa-lg"></span><strong> Menu</strong></NavLink>
                            </NavItem>
                            <NavItem className="ml-1">
                                <NavLink className="nav-link" to='/aboutus' ><span className="fa fa-info fa-lg"></span><strong> About Us</strong></NavLink>
                            </NavItem>
                            
                            <NavItem className="ml-1">
                                <NavLink className="nav-link" to='/contactus' ><span className="fa fa-address-card fa-lg"></span><strong> Contact Us</strong></NavLink>
                            </NavItem >
                            </Nav>
                            <Nav className=" ma " >
                                <NavItem className="ml-5 mr-5">
                                <button type="button" onClick={this.toggleModal} class="btn btn-light"><span className="fa fa-sign-in fa-lg"></span> Login</button>
                                
                                </NavItem>
                            </Nav>
                        </Collapse>
                 
                </Navbar>
           <div class="card card-head bg-dark text-white ">
  <img class="card-img card-img-css" src="/assets/images/back4.jpg" alt="Card image"/>
  <div class="card-img-overlay  ca ">
  <div class="row ">
  <div class=" col-md-6  food d-none d-sm-block">
  <h1 class="food">Delicious Food  </h1>
                                <p>People who love to eat are always the best people</p>
  </div>
  <div class="col-md-4 offset-md-1 d-none d-sm-block signup ">
  <div class="card">
  <div class="card-body">
    <h5 class="card-title">  <div>
                 
                    
                    <strong class="  ml-5 ">
                     Create  Account
                    </strong>
                    </div> </h5>
                    <hr/>
    <Form  onSubmit={this.handleLogin} >
         
                        <FormGroup className="row" >
                    <a class="btn btn-block btn-social btn-facebook">
                   <span class="fa fa-facebook"></span><span class="ml-4">  Sign up  with Facebook </span></a> 
                             
                          </FormGroup>
                          <FormGroup className="row" >
                    <a class="btn btn-block btn-social btn-google">
                   <span class="fa fa-google"></span><span class="ml-4">  Sign up with Google </span></a> 
                          </FormGroup>
                          <FormGroup className="row" >
                              <div class="line"><span class="te"> <strong> Or </strong></span></div>
                          </FormGroup>
                          <FormGroup className="row" >
                              
                              <Input type="text" placeholder="User Name" className="col-12" id="username" name="username"
                                  innerRef={(input) => this.username = input} />
                          </FormGroup>
                            <FormGroup className="row" >
                              
                                <Input type="text" placeholder="Email Address" className="col-12" id="email" name="username"
                                    innerRef={(input) => this.username = input} />
                            </FormGroup>
                            <FormGroup className="row mb-0">
                                
                                <Input type="password" placeholder="Password "className="col-12" id="password" name="password"
                                    innerRef={(input) => this.password = input}  />
                            </FormGroup>
                           
                          
                           
                            <div class="row mt-2 ml-4 mr-4">
                            
                            <button type="button" class="btn btn-success  btn-block ">Register</button>
                            </div>
                            <small class="mt-1 ml-5 ">Do u have an account? <a href="#" onClick={this.toggleModal}>Login</a> </small>
                           
                        </Form>
   
  </div>
</div>
 
  </div>

  <div class="d-block d-sm-none ">
<div class="row ">
<h3 class="col-12 m-head"><strong> Delicious Food</strong></h3>
<p class="col-12 m-head"> People who love to eat are always the best people</p>
<div class="col-3 but-1 offset-3">

<button type="button" onClick={this.toggleModal} class="btn btn-sm but-11 "><span className="fa fa-sign-in fa-sm"></span><strong> Login</strong></button>
</div>
<div class="col-6">
<button type="button" onClick={this.toggleModal2} class="btn btn-sm but-2  "><strong>Register</strong></button>
</div>
</div>


  </div>
  </div>

  </div>
   
  </div>
             
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal} className="model" >
          

           
                    <ModalHeader className="model-header"  toggle={this.toggleModal} >
                   <div>
                    <img class="im" src='assets/images/logo2.png'  height="40" width="40" alt='SS Food Factory'  />
                    
                    <strong class=" head shadow-lg">
                    User Login
                    </strong>
                    </div>
                  </ModalHeader>
                    <ModalBody className="model-body">
                    

                        <Form  onSubmit={this.handleLogin} >
         
                        <FormGroup className="row" >
                    <a class="btn btn-block btn-social btn-facebook">
                   <span class="fa fa-facebook"></span><span class="ml-4">  Login  with Facebook </span></a> 
                             
                          </FormGroup>
                          <FormGroup className="row" >
                    <a class="btn btn-block btn-social btn-google">
                   <span class="fa fa-google"></span><span class="ml-4">  Login  with Google </span></a> 
                             



                          </FormGroup>
                          <FormGroup className="row" >
                              <div class="line"><span class="te"> <strong> Or </strong></span></div>
                          </FormGroup>
                          
                            <FormGroup className="row" >
                              
                                <Input type="text" placeholder="Email Address" className="col-12" id="email" name="username"
                                    innerRef={(input) => this.username = input} />
                            </FormGroup>
                            <FormGroup className="row mb-0">
                                
                                <Input type="password" placeholder="Password "className="col-12" id="password" name="password"
                                    innerRef={(input) => this.password = input}  />
                            </FormGroup>
                            <a href="#" class="ml-0">
                            <small id="emailHelp" class=" "> Forget Password?</small>
                            </a>
                          
                            <FormGroup check className="d-flex cc mt-1  ">
                            <div class="ml-0 mt-1 ">
                            <Input type="checkbox" name="remember" className=""
                                    innerRef={(input) => this.remember = input}  />
                            </div>
                            <div class="">
                            <Label check className=" align-self-center    rem">  Keep me logged in   </Label>
                                  
                            </div>
                               
                                  
                               
                            </FormGroup>
                            <div class="row mt-2 ml-4 mr-4">
                            
                            <button type="button" class="btn btn-success  btn-block ">Login</button>
                            </div>
                            <small class="mt-1 ml-5 ">Don't have an account? <a href="#" onClick={this.toggleModal12}>Sign Up</a> </small>
                        </Form>
                        

                    </ModalBody>
                 
                </Modal>
                <Modal isOpen={this.state.isModalOpen2} toggle={this.toggleModal2} className="model" >
          

           
                    <ModalHeader className="model-header"  toggle={this.toggleModal2} >
                   
                   <h5 class="card-title pt-2"> 
                 
                    
                 <strong class="sign-head">
                  Create  Account
                 </strong>
                 </h5>
                
                
                  </ModalHeader>
                    <ModalBody className="model-body">
                    
                 
 <Form  onSubmit={this.handleLogin} >
      
                    
                       <FormGroup className="row" >
                 <a class="btn btn-block btn-social btn-google">
                <span class="fa fa-google"></span><span class="ml-4">  Sign up with Google </span></a> 
                       </FormGroup>
                       <FormGroup className="row" >
                 <a class="btn btn-block btn-social btn-facebook">
                <span class="fa fa-facebook"></span><span class="ml-4">  Sign up  with Facebook </span></a> 
                          
                       </FormGroup>
                       <FormGroup className="row" >
                           <div class="line"><span class="te"> <strong> Or </strong></span></div>
                       </FormGroup>
                       <FormGroup className="row" >
                           
                           <Input type="text" placeholder="User Name" className="col-12" id="username" name="username"
                               innerRef={(input) => this.username = input} />
                       </FormGroup>
                         <FormGroup className="row" >
                           
                             <Input type="text" placeholder="Email Address" className="col-12" id="email" name="username"
                                 innerRef={(input) => this.username = input} />
                         </FormGroup>
                         <FormGroup className="row mb-0">
                             
                             <Input type="password" placeholder="Password "className="col-12" id="password" name="password"
                                 innerRef={(input) => this.password = input}  />
                         </FormGroup>
                        
                       
                        
                         <div class="row mt-2 ml-4 mr-4">
                         
                         <button type="button" class="btn btn-success  btn-block ">Register</button>
                         </div>
                         <small class="mt-1 ml-5 ">Do u have an account? <a href="#" onClick={this.toggleModal12}>Login</a> </small>
                        
                     </Form>
                        

                    </ModalBody>
                 
                </Modal>
            </div>
        );
    }
}


export default HeaderComponent;
