import React from 'react'
import { Card, CardImg, CardText, CardBody,
  CardTitle,CardImgOverlay, CardSubtitle,Media} from 'reactstrap';

  function RenderLeader({leader}){
    return(
        
    
    <div key={leader.id} className="mt-3">
      <Media tag="li" className="row">
        <Media  className="col-12 col-md-3 mb-2">
        <img  className="dia "  src={leader.image}/>
            
        </Media>
        <Media body className=" col-12 col-md-9 pl-3 pr-3">
          <Media heading><h4>{leader.name}</h4></Media>
            <h6>{leader.designation}</h6>
          <p>{leader.description}</p>
        </Media>
      </Media>
    </div>
      
    
    );
    
    
    }


function RenderCards({item}) {
  const menu=item.map(dish=>{
    return(
      <div key={dish.id} className="col-12 col-md-6 mt-2 mr-0 mb-2">
              <Card className="card1">
              
              <CardImg width="100%" object src={dish.image} alt={dish.name}  />
              <CardImgOverlay >
                    <CardTitle className="font-weight-bold title" heading="true">{dish.name}</CardTitle>
                  </CardImgOverlay>
                <CardBody>
        <CardTitle className="font-weight-bold">{dish.name}</CardTitle>
        {dish.designation ? <CardSubtitle>{dish.designation}</CardSubtitle> : null }
        <CardText>{dish.description}</CardText>
        </CardBody>
              </Card>
            </div>

    )
 
  }

)
  return(
    <div class="row pt-3">
{menu}
    </div>
  );

}
function Home(props) {

 
  return (
    <div class="homebody" id={'hom'} name="hom">
<div class="container ">
<div className="row m-0">
            <div className="col-12 mt-3">
            <div class="d-flex justify-content-center">
            <h4>Frequently Ordered Dishes</h4>
            </div>
                        
                        <div class="stline"></div>
                    </div>  
              
                <RenderCards item={props.dishes} />
                
                
                
                
                  <div class="col-12 mt-2">
                  <div class="stline mt-1 mb-1"></div>
                  <div class="d-flex justify-content-center">
                  <h4>Promotions </h4>
            </div>
              
                  <div class="stline"></div>
                  </div>
               
                    <RenderCards item={props.promotions} />
               
                <div className="col-12 col-md-12 ">
                <div class="row m-0">
                  <div class="col-12 pt-2">

                  <div class="stline mb-1"></div>
                  <h4>Developer </h4>
                  <div class="stline"></div>
                  </div>
                
                </div>
                <RenderLeader leader={props.leader} />
                </div>
            </div>
</div>
         
    </div>

       
  )
}

export default Home;
