import React from'react';
import {Card, CardImg, CardImgOverlay, CardText, CardBody,
  CardTitle, Breadcrumb, BreadcrumbItem,CardSubtitle } from 'reactstrap';

  import { Link } from 'react-router-dom';



function Menu(props){

    const menu=props.dishes.map(dish=>{
      return(
        <div key={dish.id} className="col-12 col-md-6 mt-2 mb-2 ">
                <Card className="">
                <Link to={`/menu/${dish.id}`} >
                      <CardImg width="100%" object src={dish.image} alt={dish.name} data-toggle="tooltip" data-placement="top" />
                 
                  <CardImgOverlay >
                    <CardTitle className="font-weight-bold title" heading="true">{dish.name}</CardTitle>
                  </CardImgOverlay>
                  </Link>
                  <CardBody>
          <CardTitle className="font-weight-bold">{dish.name}</CardTitle>
          {dish.designation ? <CardSubtitle>{dish.designation}</CardSubtitle> : null }
          <CardText>{dish.description}</CardText>
          </CardBody>
                </Card>
              </div>

      )
   
    }

 )
    return(
     <div class="menubody">
  <div className="container">
        <div className="row">
                    <Breadcrumb className="mt-2">
                        <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                        <BreadcrumbItem active>Menu</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h4>Menu <small class=""> ( Please click on the below dishes to view their dish details)</small></h4>
                        <hr />
                    </div>                
                </div>
         <div className="row">
       
             {menu}
      
         </div>
        
      </div>


     </div>
    

    );
  }

export default Menu;

